body {
  margin: 0;
  padding: 0;
  background: #F2F4F5;
  color: #1E1E1F;
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
}

#root {
  margin: 0 24px;
  position: relative;
  min-height: 100vh;
}
.create-todo-button {
    background-color: rgb(97, 217, 250);
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: #FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;
    z-index: 1;
    -webkit-transform: rotate(0);
            transform: rotate(0);
    transition: .3s ease;
}

.create-todo-button:hover {
    -webkit-transform: rotate(224deg);
            transform: rotate(224deg);
}
.todo-counter {
    font-size: 24px;
    text-align: center;
    margin: 0;
    padding: 48px;
}
.loading-todo-container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
}

.loading-todo-complete-icon,
.loading-todo-delete-icon {
    border-radius: 50px;
    height: 48px;
    width: 48px;
}

.loading-todo-complete-icon {
    position: absolute;
    left: 12px;
}

.loading-todo-delete-icon {
    position: absolute;
    top: -24px;
    right: 0;
}

.loading-todo-text {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}

.loading-todo-container,
.loading-todo-complete-icon,
.loading-todo-delete-icon {
    background: linear-gradient(90deg, rgba(250, 250, 250, 1), rgb(97, 217, 250));
    background-size: 400% 400%;
    -webkit-animation: laodingAnimation 3s ease-in-out;
            animation: laodingAnimation 3s ease-in-out;
}

@-webkit-keyframes loadingAnimation {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}

@keyframes loadingAnimation {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}
.todo-search {
    background: #F9FBFC;
    border-radius: 2px;
    border: 2px solid #202329;
    margin: 0 24px;
    height: 64px;
    width: calc(100% - 62px);
    font-size: 24px;
    text-align: center;
    font-family: 'Montserrat';
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  }
  
  .todo-search::-webkit-input-placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  .todo-search::placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  .todo-search:focus {
    outline-color: #61DAFA;
  }
form {
    width: 90%;
    max-width: 300px;
    background-color: #fff;
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

label {
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    color: #1E1E1F;
    margin-bottom: 26px;
}

textarea {
    background-color: #F9FBFC;
    border: 2px solid #202329;
    border-radius: 2px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
}

textarea::-webkit-input-placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
}

textarea::placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
}

textarea:focus {
    outline-color: #61DAFA;
}

.todo-form-button-container {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.todo-form-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: 'Montserrat';
}

.todo-form-button--add {
    background: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
}

.todo-form-button--cancel {
    background: transparent;
}
ul {
    margin: 0;
    padding: 0 0 56px 0;
    list-style: none;
  }
.icon-container {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
}

.icon-container-check {
    position: absolute;
    left: 12px;
}

.icon-container-delete {
    position: absolute;
    top: -24px;
    right: 0;
}

.icon-svg {
    fill: white;
    width: 24px;
    height: 24px;
    transition: fill .15s;
}

.icon-container:hover .icon-svg-delete {
    fill: red;
}
.todo-item {
    background-color: #FAFAFA;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
}

.todo-item-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}

.todo-item-p-complete {
    text-decoration: line-through;
}
.modal-background {
    background: rgba(32,35,41,.8);
    position: fixed;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
  }
